}

.activeBtn{
    border-top:2px solid #ff9900;
}
.movieInfo{
    position:relative;
    overflow: hidden;
}
.movieInfoBackground{
    filter: blur(8px);
    -webkit-filter: blur(8px);
    opacity: 0.4;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.poster{
    width: 400px;
    height:600px;
    border-radius:10px; 
    box-shadow: 0px 0px 22px 7px #000000;
}

.details{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding:0 40px;
}

.infoMovieTitle{
    font-size: 40px;
    margin-bottom:20px;
}


.contentInfo{
    width: 80%;
    position:absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: -webkit-flex;
    display: flex;
}

.synopsis{
    color:rgb(255, 153, 0);
    margin-bottom: 5px;
}

.overview{
    line-height: 25px;
}
.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
  }
  
  .video-responsive iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
  }
a{text-decoration: none;color:white;}
ul{list-style: none;}


.navbar{
    background: black;
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top:0;
    left:0;
    overflow-x: hidden;
    transition: 0.5s;
    font-size: 20px;
}

.active{
    width: 15%;
}

.showSidebar{
    position: absolute;
    right: 0;
    display: inline-block;
    cursor:pointer;
}

.blank{display: none;}

.hamburguer{
    display: block;
}


.fixedBar{
    padding:0 15%;
    background:rgb(0, 0, 0);
}

.logo{
    width: 35px;
}
* {box-sizing:border-box}
.slideshow{
    width: 100%;
    height: 500px;
    overflow:hidden;
    margin:0;
    padding:0;
}

.each-slide{
    width: 100%;
    height: 500px;
    position: relative;
}

.gradient{
    position: absolute;
    z-index: 1;
    top:0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right,#000000 40%, transparent 70%);
}

.each-slide img{
    width: 60%;
    float: right;
    z-index: -1;
}

.indicators{
    width: 100%;
    top:55%;
    position: absolute;
}

.indicator{
    max-width: 140px;
    max-height:190px;
    cursor:pointer;
    margin:0 10px;
}

.indicator img{
    max-width: 100%;
    max-height:100%;
    border-radius:10px;
    transition:.3s;
    opacity: 0.5;
}

.voteAverage{
    display: inline-block;
    background:yellow;
    color:black;
    padding: 0 10px;
}
.indicator img:hover{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.2);  
    transform: scale(1.2);
    opacity: 1;
}

.indicator.active img{
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: scale(1.2);  
    transform: scale(1.2); 
    box-shadow: 0px 0px 10px 2px #000000;
    border:1px solid white;
    opacity: 1;
}

.slideInfo{
    position:absolute;
    top:10%;
    display: -webkit-flex;
    display: flex;
    z-index: 999;
    width: 40vw;
    margin:0 0 0 150px;
    height:100%;
}

.slideInfo div h1{
    font-size: 40px;
}

.slideInfo .playButton{
    color:black;
    display: -webkit-inline-flex;
    display: inline-flex;
    background: white;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.playButton{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.play{
    color:black;
    margin-right:8px;
}
.backdrop_path{
    box-shadow: inset 10px 10px 50px rgb(0, 0, 0);
}

.react-slideshow-container .nav{
    color:tomato;
    height: 100%;
    transition: 0.2s;
}

.react-slideshow-container .nav:hover{
    background: rgba(0, 0, 0, 0.534);
}

